import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { SocialChatComponent } from './social-chat.component';
import { ForumCategoryComponent } from './components/forum-category/forum-category.component';
import { ForumComponent } from './components/forum/forum.component';


const routes: Routes = [
	{ path: '', component: SocialChatComponent,
	
		children: [
			{ path: '', redirectTo: 'categories', pathMatch: 'full'},
			{ path: 'categories', component: ForumCategoryComponent },
			{ path: 'forum/:id', component: ForumComponent },
			{ path: '**', redirectTo: 'categories', pathMatch: 'full'}
		]
	}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SocialChatRoutingModule { }
