import { Component, OnInit, ɵConsole} from '@angular/core';
import {Router,NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { ForumTopicsService } from '../../../../api/services/forum-topics.service';
import { ForumPostsService } from '../../../../api/services/forum-posts.service';
import { SharedService } from '../../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-forum',
  templateUrl: './forum.component.html',
  styleUrls: ['./forum.component.scss']
})

export class ForumComponent implements OnInit {

  user:any; 
  getallTopics:any;
  panelOpenState :boolean;
  commentHide:boolean;
  loaded:boolean;  
  
  uploadFileItem:any;
  
  constructor(
    private router: Router, 
    private route: ActivatedRoute,
    private ForumPostsService:ForumPostsService,
    private ForumTopicsService:ForumTopicsService,
    private sharedService:SharedService,  
    private cookieService:CookieService 
  ){}

  ngOnInit() {
   
    this.user = {}
    this.getallTopics = []   
    
    const apartmentId = parseInt(this.cookieService.get('apartmentId'))
    //console.log(this.route.params['value'].id)


    this.ForumTopicsService.getAllForumTopicsByApartmentId(apartmentId).subscribe((res : any) => {
      
      if(res){
          this.loaded = true   
          this.getallTopics = res.reverse(); 
          this.user.name = res[0].insertPersonName
      }
    })

  }

  uploadImage(event:any) {

    if (event.target.files && event.target.files[0]) {
        var reader = new FileReader();      
        reader.readAsDataURL(event.target.files[0]);
        reader.onload = (event) => {
          this.user.postImg = reader.result;       
        }
    }

  }

  openComments(event){
    
    event.hide = !event.hide
    if(!event.hasOwnProperty('postArr')){
      event.postArr = []
    }
    if(event.hide) {
      this.ForumPostsService.getAllForumPostsByTopicsId(event.id).subscribe((res:any) => { 
        event.postArr = res
      },
      error => {
        console.log(error)
      });
    }
    
  }

  addPost(event){
  
    let topicsPost = { 
      "description": event.postDescription,
      "likes": 0,
      "forumTopicsId": event.id,     
      "img": "string",
      "active": true,
      "insertedBy": 0,
      "updatedBy": 0,
    }
    event.totalComments++
    event.postArr.push(topicsPost)
    event.postDescription = ""
    this.ForumPostsService.addForumPosts(topicsPost).subscribe((res:any) => {       
    },
    error => {
      console.log(error)
    });
    
   
  }
  
  likesUpdate(event){
    event.likeBtn = 0
    event.toggle = true ? 'Disable' : 'Disable';
    //event.status = event.toggle ? 'Disable' : 'Disable';
    this.ForumTopicsService.addLike(event.id).subscribe((res:any) => { 
      event.likes++        
    },
    error => {
      console.log(error)
    })
  }
 
  userPost(){
   
    let postDetails = { 
      
      "apartmentId": parseInt(this.cookieService.get('apartmentId')),
      "subject": this.user.subject,
      "description": this.user.description,
      "likes": 0,      
      "img": this.user.postImg,
      "categoryId": 1,
      "active": true, 
      "insertedBy": 1,
      "updatedBy":0   
    }

    this.getallTopics.unshift(postDetails)
    this.panelOpenState = false 
    this.user = {}; 
   
    this.ForumTopicsService.addForumTopics(postDetails).subscribe((res:any) => {
       this.sharedService.setAlertMessage("Posted successfully");        
    },
    error => {
      console.log(error)
    });

   }
}

